const modals = () => {
  function bindModal(
    triggerSelector,
    modalSelector,
    closeSelector,
    closeClickOverlay = true,
    validateTriggerClass = null
  ) {
    const trigger = document.querySelectorAll(triggerSelector),
      modal = document.querySelector(modalSelector),
      close = document.querySelector(closeSelector),
      windows = document.querySelectorAll('[data-modal]'),
      scroll = calcScroll();

    trigger.forEach(item => {
      item.addEventListener('click', e => {
        if (e.target) {
          e.preventDefault();
        }

        if (validateTriggerClass) {
          const inputFields = modal.querySelectorAll('input');
          let isEmpty = true;
          let isCheckboxEmpty = false;

          switch (inputFields[0].type) {
            case 'text':
              inputFields.forEach(item => {
                const isSomeInputEmpty = [...inputFields].some(
                  item => !item.value
                );

                if (isSomeInputEmpty) {
                  document.querySelector(
                    `.${validateTriggerClass}`
                  ).style.pointerEvents = 'none';
                  document.querySelector(
                    `.${validateTriggerClass}`
                  ).style.opacity = '0.4';
                }

                item.addEventListener('input', () => {
                  const isSomeInputEmpty = [...inputFields].some(
                    item => !item.value
                  );

                  isSomeInputEmpty ? (isEmpty = true) : (isEmpty = false);

                  if (isEmpty) {
                    document.querySelector(
                      `.${validateTriggerClass}`
                    ).style.pointerEvents = 'none';
                    document.querySelector(
                      `.${validateTriggerClass}`
                    ).style.opacity = '0.4';
                  } else {
                    document.querySelector(
                      `.${validateTriggerClass}`
                    ).style.pointerEvents = 'auto';
                    document.querySelector(
                      `.${validateTriggerClass}`
                    ).style.opacity = '1';
                  }
                });
              });
              break;
            case 'checkbox':
              inputFields.forEach(item => {
                if (
                  window.getComputedStyle(item.nextElementSibling, '::before')
                    .content != 'none'
                ) {
                  isCheckboxEmpty = true;
                }
              });

              if (isCheckboxEmpty) {
                document.querySelector(
                  `.${validateTriggerClass}`
                ).style.pointerEvents = 'auto';
                document.querySelector(
                  `.${validateTriggerClass}`
                ).style.opacity = '1';
              } else {
                document.querySelector(
                  `.${validateTriggerClass}`
                ).style.pointerEvents = 'none';
                document.querySelector(
                  `.${validateTriggerClass}`
                ).style.opacity = '0.4';
              }

              inputFields.forEach(item => {
                item.addEventListener('change', () => {
                  document.querySelector(
                    `.${validateTriggerClass}`
                  ).style.pointerEvents = 'auto';
                  document.querySelector(
                    `.${validateTriggerClass}`
                  ).style.opacity = '1';
                });
              });
              break;
          }
        }

        windows.forEach(item => {
          item.style.display = 'none';
        });

        modal.style.display = 'block';
        document.body.style.overflow = 'hidden';
        document.body.style.marginRight = `${scroll}px`;
      });
    });

    close.addEventListener('click', () => {
      windows.forEach(item => {
        item.style.display = 'none';
      });

      modal.style.display = 'none';
      document.body.style.overflow = '';
      document.body.style.marginRight = '0px';
    });

    modal.addEventListener('click', e => {
      if (e.target == modal && closeClickOverlay) {
        windows.forEach(item => {
          item.style.display = 'none';
        });

        modal.style.display = 'none';
        document.body.style.overflow = '';
        document.body.style.marginRight = '0px';
      }
    });
  }

  function showModalByTime(selector, time) {
    setTimeout(function () {
      let display;

      document.querySelectorAll('[data-modal]').forEach(item => {
        if (getComputedStyle(item).display !== 'none') {
          display = 'block';
        }
      });

      if (!display) {
        document.querySelector(selector).style.display = 'block';
        document.body.style.overflow = 'hidden';
      }
    }, time);
  }

  function calcScroll() {
    let div = document.createElement('div');

    div.style.width = '50px';
    div.style.height = '50px';
    div.style.overflowY = 'scroll';
    div.style.visibility = 'hidden';

    document.body.appendChild(div);

    let scrollWidth = div.offsetWidth - div.clientWidth;
    div.remove();

    return scrollWidth;
  }

  bindModal('.button-design', '.popup-design', '.popup-design .popup-close');
  bindModal(
    '.button-consultation',
    '.popup-consultation',
    '.popup-consultation .popup-close'
  );
  bindModal('.fixed-gift', '.popup-gift', '.popup-gift .popup-close');

  showModalByTime('.popup-consultation', 5000);
};

export default modals;
